<template>
  <div class="islike">
    <van-nav-bar title="我的收藏" left-text="返回" left-arrow @click-left="$router.go(-1)" />
    <van-divider />

    <div class="main" v-if="!islikeList.length">
      <van-empty description="还没有收藏，赶紧去看看吧" />

      <recomment></recomment>
    </div>

    <div class="content clearfix" v-else>
      <div
        class="item clearfix"
        v-for="(item,index) in islikeList"
        :key="index"
        
      >
        <div class="img fl" @click="goDetalis(item.pid)">
          <img :src="item.smallImg" alt class="img_auto" />
        </div>
        <div class="text fl">
          <p>{{item.name}}</p>
          <p class="one-text p">{{item.desc}}</p>
          <span>
            <font>￥</font>
            {{item.price}}
          </span>
          <van-icon name="delete" class="fr" size="18px" @click="del(item.pid,index)" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import recomment from "../components/recomment.vue";
export default {
  data() {
    return {
      islikeList: ""
    };
  },
  created() {
    this.findAllLike();
  },
  methods: {
    goDetalis(pid) {
      //
      this.$router.push({
        path: "/detalis",
        query: {
          pid
        }
      });
    },
    del(pid, index) {
      
      let tokenString = localStorage.getItem("__jigng");
      this.axios({
        method: "POST",
        url: "/notlike",
        data: {
          appkey: this.appkey,
          tokenString,
          pid
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 900) {
            this.islikeList.splice(index, 1);
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    findAllLike() {
      let tokenString = localStorage.getItem("__jigng");
      this.axios({
        method: "GET",
        url: "/findAllLike",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(res => {
          
          if (res.data.code == 2000) {
            this.islikeList = res.data.result;
            // 
          }
        })
        .catch(err => {
          
        });
    }
  },
  components: {
    recomment
  }
};
</script>

<style lang="less" scoped>
.islike {
  .content {
    width: 95%;
    margin: 0 auto;
    .item {
      width: 100%;
      margin-top: 15px;
      .img {
        width: 35%;
        height: 100px;
        img {
          width: 80%;
          margin: 0 auto;
          border-radius: 20px;
        }
      }
      .text {
        width: 60%;
        text-align: center;
        .p {
          margin-top: 20px;
        }
        span {
          color: red;
          font-size: 15px;
        }
        font {
          font-size: 12px;
        }
      }
    }
  }
}
</style>